.dashboard{
  display: flex;
  flex-flow: column;
  position: relative;
  &__head{
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-weight: bold;
    font-size: 2.25rem;
    letter-spacing: var(--spacing);
    background: #0A1F34;
    margin-top: 1px;
    align-items: flex-start;
    color: white;
    padding-bottom: 24px;
    padding-top: 14px;
    padding-left: 16px;
  }
  &__below{
    display: flex;
    flex-flow: row;
    column-gap: 28px;
    position: relative;
    &__element{
      display: flex;
      flex-flow: column;
      position: relative;
      border-radius: 25px;
      background: white;
      z-index: 0;
      border: solid 1px;
      box-shadow: 0 0 10px rgb(0 0 0 / 80%);
      &__head{
        position: relative;
        display: flex;
        justify-content: center;
      }
      &__body{
        text-align: center;
      }
    }
  }
  &__button{
    &__style{
      display: block;
      height: 40px;
      width: fit-content;
      border-radius: 5px;
      font-family:  montserrat,sans-serif;
      font-size: 20px;
      color: white;
      background-color: #138BBF;
      margin: auto;
      margin-top: 32px;
    }
    &__style:hover{
      color: var(--clr-primary-5);
      background: var(--clr-primary-8);
      border-color: var(--clr-primary-8);
      background: var(--clr-primary-10);
      --transition: all 0.3s linear;
    }
  }
}
